                <h1 class="title">제11조 (이용자의 개인정보보호)</h1>
                "회사"는 관련법령이 정하는 바에 따라서 "이용자"의 개인정보를 보호하기 위하여 노력합니다. "이용자"의 개인정보보호에 관한 사항은 관련법령 및 "회사"가 정하는 "개인정보처리방침"에 정한 바에 따릅니다.<br>

